.loaderContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: -100px 0 0 0;
}

.ip__track {
    stroke: hsl(223, 90%, 90%);
    transition: stroke 0.5s;
}

.ip__worm1,
.ip__worm2 {
    animation: worm1 2s linear infinite;
}

.ip__worm2 {
    animation-name: worm2;
}

/* Dark theme */
@media (prefers-color-scheme: dark) {
    :root {
        --bg: hsl(223, 90%, 5%);
        --fg: hsl(223, 90%, 95%);
    }

    .ip__track {
        stroke: hsl(223, 90%, 15%);
    }
}

/* Animation */
@keyframes worm1 {
    from {
        stroke-dashoffset: 0;
    }
    50% {
        animation-timing-function: steps(1);
        stroke-dashoffset: -358;
    }
    50.01% {
        animation-timing-function: linear;
        stroke-dashoffset: 358;
    }
    to {
        stroke-dashoffset: 0;
    }
}

@keyframes worm2 {
    from {
        stroke-dashoffset: 358;
    }
    50% {
        stroke-dashoffset: 0;
    }
    to {
        stroke-dashoffset: -358;
    }
}