.mainContainer {
    display: flex;
    height: 100vh;
}

.todoBlock {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 60px 0 30px 0;
    gap: 30px;
    overflow-y: scroll;
    /*overflow-x: hidden;*/
    overflow-x: visible;
    /*overflow-y: hidden;*/
    /*overflow: auto;*/
    scroll-snap-align: start;
    scroll-padding-left: 50px;
}

.todos {
    display: flex;
    align-items: start;
    gap: 20px;
    flex-grow: 1;
}

.rowOrientation {
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
}

.columnOrientation {
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: center;
    justify-content: start;
}



